.sitemap__container {
  padding-bottom: 160px;
}

.site-map__item ul {
  padding: 0;
}

.sitemap__title {
  margin-bottom: 20px;
}

.site-map__wrap a, .site-map__category {
  color: var(--black);
}

.site-map__item a:hover,
.site-map__product-wc a:hover,
.site-map__category a:hover,
.site-map__category:hover,
.site-map__product-blog a:hover {
  color: var(--red);
  text-decoration: underline;
}

.site-map__wrap {
  padding-left: 20px;
}

.site-map__header {
  margin-bottom: 10px;
  font-weight: 500;
  font-size: 25px;
}

.site-map__category a {
  font-weight: 800;
}

.site-map__category {
  font-size: 19px;
  font-weight: 800;
  text-transform: uppercase;
  margin-top: 15px;
  outline: none;
  cursor: pointer;
}

.site-map__sub-category a {
  font-weight: 700;
}

.site-map__sub-category {
  cursor: pointer;
  font-size: 18px;
  margin-bottom: 5px;
  margin-left: 15px;
  margin-top: 8px;
  outline: none;
}

li.site-map__product {
  margin-left: 30px;
}

.site-map__wrapper {
  display: flex;
  flex-wrap: wrap;
}

.site-map__wrapper li {
  width: 30%;
  margin-left: 30px;
}

.site-map__product-blog {
  margin-left: 30px;
}

.site-map__product, .site-map__product-wc {
  line-height: 1.6;
  margin-bottom: 6px;
  font-size: 14px;
}

@media (max-width: 1200px) {
  .sitemap__container {
    padding-bottom: 100px;
  }
}

@media (max-width: 1000px) {
  .site-map__wrapper li {
    width: 45%;
  }
  .site-map__product, .site-map__product-wc {
    white-space: unset !important;
  }
}

@media (max-width: 768px) {
  .site-map__wrapper li {
    width: 100%;
  }
  .site-map__product, .site-map__product-wc {
    font-size: 16px !important;
  }
}

@media (max-width: 600px) {
  .sitemap__container {
    padding-bottom: 0;
  }
}

@media (min-width: 1200px) {
  .site-map__product, .site-map__product-wc {
    font-size: 14px;
  }
}
